  /* Write your own custom base styles here */
  html {
    color-scheme: light;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
  }

  html.dark {
    color-scheme: dark;
  }

  html .dark-image {
    display: none;
  }

  html .light-image {
    display: block;
  }

  html.dark .dark-image {
    display: block;
  }

  html.dark .light-image {
    display: none;
  }

  /* Hide all content that's relevant only to a specific platform */
  html.platform-mac [data-platform='win'] {
    display: none;
  }

  html.platform-win [data-platform='mac'] {
    display: none;
  }

  html,
  body {
    padding: 0;
    margin: 0;
  }

  @media screen and (max-width: 1023px) {
    body {
      overflow-x: hidden;
    }
  }

  /* Start purging... */
  /* Force GPU Accelerated scrolling, credit: Twitter Lite */
  .scrolling-gpu {
    transform: translateZ(0);
  }

  @layer utilities {
    .text-7xl {
      font-size: 5rem;
    }

    .text-8xl {
      font-size: 6rem;
    }
  }

  a>code {
    color: #087ea4 !important;
    /* blue-50 */
    text-decoration: none !important;
  }

  html.dark a>code {
    color: #58c4dc !important;
    /* blue-40 */
  }

  .text-code {
    font-size: calc(1em - 10%) !important;
  }

  .text-gradient {
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    box-decoration-break: clone;
    background-repeat: no-repeat;
    color: transparent;
  }

  .text-gradient-electric-blue {
    background-image: linear-gradient(45deg, #61dafb, #0072ff);
  }

  /* Stop purging. */
  /* Your own custom utilities */

  details {
    margin-bottom: 1rem;
  }
