.translate-y-full {
  transform: translateY(100%);
}

.translate-y-0 {
  transform: translateY(0);
}
@keyframes slide-up {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}

.slide-up {
  animation: slide-up 0.3s ease-out forwards;
}
/* Apply this style to the elements you want to make unselectable */
.select-none {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none;    /* Firefox */
  -ms-user-select: none;     /* Internet Explorer/Edge */
}
@layer utilities {
  /* Show tabs by default, but hide them at 1240px and larger */
  .custom-hide-lg {
    opacity: 1;
    display: flex;
    transition: opacity 0.3s ease-in-out;
  }

  @media (min-width: 1240px) {
    .custom-hide-lg {
      opacity: 0 !important;
      pointer-events: none !important;
      display: none !important; /* Hide the tabs */
    }
  }

  /* Ensure the tabs are hidden by default on large screens */
  .custom-show-lg {
    display: none;
    color: #114092;
  }

  @media (max-width: 1240px) {
    .custom-show-lg {
      display: flex;
    }
  }
}
@layer utilities {
  .custom-hide-sm-md {
    display: block;


    @media (max-width: 767px) {
      display: none;
    }

    @media (min-width: 768px) and (max-width: 1239px) {
      display: none;
    }


    @media (min-width: 1240px) {
      display: block;
    }
  }
}

@layer utilities {
  .custom-hide-sm-md-header {
    display: block;


    @media (max-width: 767px) {
      display: none;
    }

    @media (min-width: 768px) and (max-width: 1239px) {
      display: none;
    }


    @media (min-width: 1240px) {
      display: block;
    }
  }
}
