.region-reverse {
  display: flex;
  justify-content: flex-end;
  flex-direction: row-reverse;
}

.round {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-width: 150px;
}

.game {
  border-radius: 6px;
  margin-bottom: 5px;
  width: 200px;
  display: flex;
  flex-direction: column;
}

.team {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  cursor: pointer;
  z-index: 100;
  user-select: none;
}

.team {
  border: 4px solid transparent;
}
.team:hover {
  border: 4px solid #3b82f6;
}

.team img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.team p {
  margin: 0;
  font-size: 14px;
}
.MuiAutocomplete-popper {
  z-index: 21 !important;
}